import qs from "qs";
import { $helper } from "./https";

// ç™¾ç§‘æ”»ç•¥
let WikiPost = {
    // è·å–æœ€æ–°æ”»ç•¥
    newest(type, id, supply = 1) {
        if (!id) return;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post`,
            params: { type: type, source_id: id, supply: supply },
        });
    },

    // è·å–æœ€æ–°æ”»ç•¥åˆ—è¡¨
    newests(type) {
        return $helper()({
            method: "GET",
            url: `/api/wiki/posts/newest`,
            params: { type: type },
        });
    },

    // è·å–å†å²ç‰ˆæœ¬
    versions(type, id) {
        if (!id) return;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post/versions`,
            params: { type: type, source_id: id },
        });
    },

    // è·å–æŒ‡å®šæ”»ç•¥
    view(post_id) {
        if (!post_id) return null;
        return $helper()({
            method: "GET",
            url: `/api/wiki/post/${post_id}`,
        });
    },

    // åˆ›å»º/æ›´æ–°æ”»ç•¥
    save(params) {
        return $helper()({
            method: "POST",
            url: `/api/wiki/post`,
            data: qs.stringify({ post: params }),
        });
    },

    // æˆ‘çš„ç™¾ç§‘æ”»ç•¥åˆ—è¡¨
    myList(params) {
        return $helper()({
            method: "GET",
            url: `/api/my/wiki/posts`,
            params: params,
        });
    },

    // åˆ é™¤æˆ‘çš„ç™¾ç§‘æ”»ç•¥
    myRemove(post_id) {
        if (!post_id) return null;
        return $helper()({
            method: "PUT",
            url: `/api/my/wiki/post/${post_id}/remove`,
        });
    },
};

